/**
 * The namespace represents an application interface to provide <a href="Tag/index.html">Cortona3D Tag JavaScript API</a>.
 * 
 * @example
 * Cortona3DSolo.use('tag');
 * Cortona3DSolo.app.initialize()
 *     .then(function () {
 *          // Use Cortona3D Tag JavaScript API here.
 *          var m_model3D = new Cortona3DSolo.tag.Model3D();
 *          // ...
 *          // By default, events of the Model3D object are translated as "tag.*" events of the Solo library.
 *          Cortona3DSolo.on('tag.onSectionLoaded', function (section) {
 *              // ...
 *          });
 *     });
 * 
 * @example <caption>Using backward compatibility mode</caption>
 * // For backward compatibility, you can use the "useBackwardCompatibilityMode" option of the "tag" module.
 * Cortona3DSolo.use('tag', {
 *      useBackwardCompatibilityMode: true
 * });
 * Cortona3DSolo.app.initialize()
 *     .then(function () {
 *          // Use Cortona3D Tag JavaScript API here.
 *          // All properties of the "Cortona3DSolo.tag" namespace are exported to the global object.
 *          var m_model3D = new Model3D();
 *          // ...
 *          // Event handlers from the Model3D object must be used instead of "tag.*" events of the Solo library.
 *          m_model3D.on_section_loaded = function (section) {
 *              // ...
 *          };
 *     });
 * 
 * @namespace tag
 * @memberof module:solo
 * @since 1.7.2
 */
var Stella = require('@cortona3d/solo-core/src/res/stella.framework');

if (typeof Cortona3DSolo != 'undefined') {
    Cortona3DSolo.tag = Stella;

    Cortona3DSolo.use('tag')
        .then(function (module) {
            var key,
                events;
            if (module.config.useBackwardCompatibilityMode) {
                // Import properties from the Stella namespace to the global object for the sake of backward compatibility.
                for (key in Stella) {
                    window[key] = Stella[key];
                }
            } else {
                // Use Solo events instead of Stella.Model3D hook methods.
                events = {
                    on_fragment_loaded: 'tag.onFragmentLoaded',
                    on_section_loaded: 'tag.onSectionLoaded',
                    on_section_unloaded: 'tag.onSectionUnloaded',
                    on_click_object: 'tag.onClickObject',
                    on_over: 'tag.onOver'
                };

                for (key in events) {
                    Object.defineProperty(Stella.Model3D.instance, key, {
                        value: Cortona3DSolo.dispatch.bind(Cortona3DSolo, events[key]),
                        enumerable: true
                    });
                }
            }
        });

}

/**
 * The hook method is invoked when the 3D model of the Section fragment is loaded.
 * Used instead of the `on_fragment_loaded` event handler from the `Model3D` namespace.
 * 
 * @memberof module:solo.tag
 * @abstract 
 * @function onFragmentLoaded
 * @param {module:solo.tag.SectionFragment} fragment
 * @see The corresponding event {@link event:"tag.onFragmentLoaded" "tag.onFragmentLoaded"}
 */
/**
 * @event "tag.onFragmentLoaded"
 * @see The corresponding hook method {@link module:solo.tag.onFragmentLoaded tag.onFragmentLoaded()}
 */

/**
 * The hook method is invoked when the airplane section is loaded.
 * Used instead of the `on_section_loaded` event handler from the `Model3D` namespace.
 * 
 * @memberof module:solo.tag
 * @abstract 
 * @function onSectionLoaded
 * @param {module:solo.tag.Section} section
 * @see The corresponding event {@link event:"tag.onSectionLoaded" "tag.onSectionLoaded"}
 */
/**
 * @event "tag.onSectionLoaded"
 * @see The corresponding hook method {@link module:solo.tag.onSectionLoaded tag.onSectionLoaded()}
 */

/**
 * The hook method is invoked when the 3D model of the section is unloaded.
 * Used instead of the `on_section_unloaded` event handler from the `Model3D` namespace.
 * 
 * @memberof module:solo.tag
 * @abstract 
 * @function onSectionUnloaded
 * @param {module:solo.tag.Section} section
 * @see The corresponding event {@link event:"tag.onSectionUnloaded" "tag.onSectionUnloaded"}
 */
/**
 * @event "tag.onSectionUnloaded"
 * @see The corresponding hook method {@link module:solo.tag.onSectionUnloaded tag.onSectionUnloaded()}
 */

/**
 * The hook method is invoked when the user clicks an object in the 3D window.
 * Used instead of the `on_click_object` event handler from the `Model3D` namespace.
 * 
 * @memberof module:solo.tag
 * @abstract 
 * @function onClickObject
 * @param {module:solo.tag.Vec3f} location
 * @param {module:solo.tag.Structure|module:solo.tag.RepairDot} item
 * @param {number} button
 * @param {number} modifierKeys
 * @see The corresponding event {@link event:"tag.onClickObject" "tag.onClickObject"}
 */
/**
 * @event "tag.onClickObject"
 * @see The corresponding hook method {@link module:solo.tag.onClickObject tag.onClickObject()}
 */

/**
 * The hook method is invoked when the user moves the cursor over the 3D window.
 * Used instead of the `on_over` event handler from the `Model3D` namespace.
 * 
 * @memberof module:solo.tag
 * @abstract 
 * @function onOver
 * @param {module:solo.tag.Vec3f} location Location on the first opaque object
 * @param {module:solo.tag.Structure|module:solo.tag.RepairDot|null} item The first opaque object
 * @param {module:solo.tag.Vec3f} topmostLocation Location on the first object (including semi-transparent)
 * @see The corresponding event {@link event:"tag.onOver" "tag.onOver"}
 */
/**
 * @event "tag.onOver"
 * @see The corresponding hook method {@link module:solo.tag.onOver tag.onOver()}
 */

